#egwishlist-modal{
  text-align: center;
  .forgot-password{
    margin-bottom: 1rem;
  }
}
#egwishlist-user-products{
  .egwishlist-product ._name{
    padding: 0;
  }
}

.btn-egwishlist-add{
  .added{
    display: none;
  }
}

.btn-egwishlist-add.egwishlist-added{
  .added{
    display: inline-block;
  }
  .not-added{
    display: none;
  }
}


